nav{
    padding:0 20px ;
}
.content{
    min-height: calc(100vh - 134px);
}
.movies{
    display:grid;
    grid-template-columns:repeat(auto-fill,minmax(250px,1fr));
    gap:10px;
}
.search-btn{
    position:absolute;
    top:0;
    right:0;
}
label:not(:last-child){
    margin-right:10px;
}